import {Component, OnInit} from '@angular/core';
import {IAppAdmin} from '../../../Models/app-admin';
import {MatDialog} from '@angular/material/dialog';
import {AppAdminService} from '../../../Services/app-admin.service';
import {ToastaService} from 'ngx-toasta';
import {DeleteRoleComponent} from '../../roles/delete-role/delete-role.component';
import {IUsersAdmin} from '../../../Models/users-admin';
import {DeleteSuperComponent} from '../delete-super/delete-super.component';
import {ChangeRoleSuperComponent} from '../change-role-super/change-role-super.component';

@Component({
    selector: 'app-supervisors',
    templateUrl: './supervisors.component.html',
    styleUrls: ['./supervisors.component.css']
})
export class SupervisorsComponent implements OnInit {

    cols: any;
    products: any;
    users: IUsersAdmin[];
    userDetails: IUsersAdmin;
    display: boolean = false;
    // toastOption: ToastOptions = {
    //     title: 'Events',
    //     msg: 'This Event Has Been Deleted successfully!',
    //     showClose: true,
    //     timeout: 3000,
    //     theme: 'material'
    // };

    constructor(public dialog: MatDialog, private _AppAdminService: AppAdminService, private toastyService: ToastaService) {
    }

    //
    ngOnInit(): void {
        this.cols = [
            {field: 'id', header: 'ID'},
            {field: 'name', header: 'Name'},
            {field: 'type', header: 'Type'},
            {field: 'job_name', header: 'Job Name'}
        ];
        this.getRolesApp();
    }

    showDialog(data: IUsersAdmin) {
        this.userDetails = data;
        console.log(this.userDetails);
        this.display = true;
    }

    getRolesApp() {
        this._AppAdminService.getUsers().subscribe((res: { data }) => {
            this.users = res.data.all as IUsersAdmin[];
            // this.rolesPermations = this.roles.map(x => x.permissions);
            // console.log(this.rolesPermations);
        });
    }

    openRoleDeleteDialog(id) {
        console.log(id);
        const dialogRef = this.dialog.open(DeleteSuperComponent, {
            data: {id}
        });
        dialogRef.afterClosed().subscribe(result => {
            if (result) {
                // this.toastyService.success(this.toastOption);
                console.log(result);
                // this.eventsList.id = result;
                this.onDeleteMember(result);

            }
        });
    }

    onDeleteMember(roleId) {
        this._AppAdminService.deleteUser(roleId).subscribe(res => {
        }, error => {
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {

            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Deleted successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            this.getRolesApp();
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }

    openBlockUserDialog(row) {
        console.log(row.id);
        const dialogRef = this.dialog.open(ChangeRoleSuperComponent, {
            data: {row}
        });
        dialogRef.afterClosed().subscribe(result => {
            if (result) {
                // this.toastyService.success(this.toastOption);
                console.log(result);
                // this.eventsList.id = result;
                this.onBlockUser(result);

            }
        });
    }

    onBlockUser(rowData) {
        this._AppAdminService.getUserToggle_activate(rowData.id).subscribe(res => {
        }, error => {
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Error Happened'});
        }, () => {
            this.getRolesApp();
            if (rowData.status === 'accepted') {
                this.toastyService.success({
                    title: 'Members',
                    msg: 'This User Has Been Blocked successfully!',
                    showClose: true,
                    timeout: 3000,
                    theme: 'material'
                });
            } else {
                this.toastyService.success({
                    title: 'Members',
                    msg: 'This User Has UnBeen Blocked successfully!',
                    showClose: true,
                    timeout: 3000,
                    theme: 'material'
                });
            }
            this.getRolesApp();
            // this.displayClassDeleteDialogue = false;
            // this.messageService.add({severity: 'error', summary: 'Class', detail: 'Deleted Successfully'});
        });
    }

}
