import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {SupervisorsComponent} from './supervisors/supervisors.component';
import {AddSuperComponent} from './add-super/add-super.component';
import {UpdateSuperComponent} from './update-super/update-super.component';
import {ChangeRoleSuperComponent} from './change-role-super/change-role-super.component';
import {DeleteSuperComponent} from './delete-super/delete-super.component';

const routes: Routes = [
    {path: '', component: SupervisorsComponent},
    {path: 'add-super', component: AddSuperComponent},
    {path: 'update-super/:id', component: UpdateSuperComponent},
    {path: 'change-role-super', component: ChangeRoleSuperComponent},
    {path: 'delete-super', component: DeleteSuperComponent},
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class SupervisorsRoutingModule {
}
