import {Component, OnInit} from '@angular/core';
import {IAppAdmin, IpermationDetail} from '../../../Models/app-admin';
import {AppAdminService} from '../../../Services/app-admin.service';
import {ToastaService} from 'ngx-toasta';
import {Router} from '@angular/router';

@Component({
    selector: 'app-add-super',
    templateUrl: './add-super.component.html',
    styleUrls: ['./add-super.component.css']
})
export class AddSuperComponent implements OnInit {

    errorMessage: string[];
    permissions: IpermationDetail;
    rolesUser: IAppAdmin;
    rolesId: number[];
    hide = true;
    //
    constructor(public _AppAdminService: AppAdminService, private toastyService: ToastaService, private router: Router) {
    }

    ngOnInit(): void {
        this.getRoles();
    }

    addAdminRole() {
        this._AppAdminService.AddUser.roles = this.rolesId;

        this._AppAdminService.addUser().subscribe(() => {

        }, (error) => {
            if (error.error) {
                this.errorMessage = error.error;

            }
        }, () => {
            console.log(this._AppAdminService.AddRole);
            // this.isLoadingMoodTeacherteachers = false;
            // this.displayClassDialogue = false;
            this.errorMessage = null;
            // this.getAllteachers();
            this._AppAdminService.AddRole = {} as IAppAdmin;
            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Created successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            this.router.navigate(['/admin-panel/supervisors']);
        });
    }

    getRoles() {

        this._AppAdminService.getRoles().subscribe((res: { data, all }) => {
            this.rolesUser = res.data.all as IAppAdmin;
            // this.rolesPermations = this.roles.map(x => x.permissions);
            console.log(this.rolesUser);
        });
    }

}
