import {Component, OnInit} from '@angular/core';
import {IAppAdmin, IpermationDetail} from '../../../Models/app-admin';
import {AppAdminService} from '../../../Services/app-admin.service';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {ToastaService} from 'ngx-toasta';
import {IAddUser, IRole} from '../../../Models/users-admin';

@Component({
    selector: 'app-update-super',
    templateUrl: './update-super.component.html',
    styleUrls: ['./update-super.component.css']
})
export class UpdateSuperComponent implements OnInit {

    errorMessage: string[];
    permissions: IpermationDetail;
    roleId: number;
    updatedData: IAddUser;
    permissionsId: any[];
    rolesUser: IAddUser[];
    rolesId: number[];
    x: any[];
    hide = true;

    constructor(public _AppAdminService: AppAdminService, private route: ActivatedRoute, private toastyService: ToastaService, private router: Router) {
        this.route.params.subscribe((params: Params) => {
            this.roleId = +params.id;
            this.getRoleById(this.roleId);
        });
    }

    ngOnInit(): void {
        this.getPermassions();
    }

    updateAdminRole() {
        this._AppAdminService.updateUsers.roles = this.permissionsId;
        this._AppAdminService.updateUser(this.roleId).subscribe(() => {

        }, (error) => {
            if (error.error) {
                this.errorMessage = error.error;

            }
        }, () => {
            console.log(this._AppAdminService.AddRole);
            // this.isLoadingMoodTeacherteachers = false;
            // this.displayClassDialogue = false;
            this.errorMessage = null;
            // this.getAllteachers();
            this._AppAdminService.AddRole = {} as IAppAdmin;
            this.toastyService.success({
                title: 'Members',
                msg: 'This Role Has Been Created successfully!',
                showClose: true,
                timeout: 3000,
                theme: 'material'
            });
            this.router.navigate(['/admin-panel/roles']);
        });
    }

    getPermassions() {
        this._AppAdminService.AddRole.permissions;
        this._AppAdminService.getRoles().subscribe((res: { data, all }) => {
            this.rolesUser = res.data.all as IAddUser[];
            // this.x = this.rolesUser.map(x => x);
            // this.permissionsId = this.x.map(x => x.name);
            // console.log(this.x);
            // console.log(this.permissionsId);
            console.log(this.rolesUser);
        });
    }

    getRoleById(id: number) {
        this._AppAdminService.getUserID(id).subscribe((res: { data }) => {
            this.updatedData = res.data as IAddUser;
            console.log(this.updatedData);
        }, error => {
        }, () => {
            // this.showQuestionData();
            this.permissionsId = this.updatedData.roles.map(b => b.id);
            console.log(this.permissionsId);

            this._AppAdminService.updateUsers = this.updatedData;

            // console.log(  this.invoiceService.InvoiceUpdate.status);

        });
    }

}
